import React, { useState } from 'react';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button } from '@mui/material';
import { DateRangePicker } from 'react-date-range';
import 'react-date-range/dist/styles.css'; //  
import 'react-date-range/dist/theme/default.css'; //  
import { addDays } from 'date-fns';

//  CSS  
import './DateRangeSelector.css'; //     

const DateRangeSelector = ({ open, onClose, onRangeSelected }) => {
  const [state, setState] = useState({
    selection: {
      startDate: new Date(),
      endDate: addDays(new Date(), 7),
      key: 'selection',
    },
  });

  const handleSelect = (ranges) => {
    setState({ selection: ranges.selection });
  };

  const handleSubmit = () => {
    const { startDate, endDate } = state.selection;

    //       YYYY-MM-DD
    const formattedStartDate = startDate.toLocaleDateString('en-CA'); //   YYYY-MM-DD
    const formattedEndDate = endDate.toLocaleDateString('en-CA'); //   YYYY-MM-DD

    onRangeSelected({ startDate: formattedStartDate, endDate: formattedEndDate });

    onClose(); //     
  };

  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth="sm">
      <DialogTitle>  </DialogTitle>
      <DialogContent>
        <div className="date-range-picker-container">
          <DateRangePicker
            ranges={[state.selection]}
            onChange={handleSelect}
            showSelectionPreview={true}
            months={2} //   
            direction="horizontal"
          />
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="primary">
          
        </Button>
        <Button onClick={handleSubmit} color="primary">
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default DateRangeSelector;